$colorBgPrincipal: #213A5C;
$colorTextPrincipal: #eeeeee;
$navbarColor: #0f1a2b;
$colorTextPrincipalOpacityReduc: rgba($color: #eeeeee, $alpha: 0.3);
@mixin display_flex_row{
    display: flex;
    align-items: center;
    justify-content: center;
}

@mixin display_flex_column{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
$fontTitle: 'Montserrat', sans-serif;
$fontParagraph: 'Hind', sans-serif;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}
h1,h2,h3,h4,h5,h6 {
    font-family: $fontTitle;
    color: $colorTextPrincipal;
}

p {
    font-family: $fontParagraph;
    color: $colorTextPrincipal;
}

body {
    background: $colorBgPrincipal;
    min-height: 100vh;
}
header{
    height: 40vh;
    background-image: url(../ressources/images/header/header-img.jpg);
    background-color: rgba($color: #213A5C, $alpha: 0.9);
    background-position: center center;
    background-blend-mode: multiply;
    text-align: center;

    h1{
        margin-top: 12vh;
        font-size: 3.5em;
        font-weight: 800;
    }
}
.navbar{
    height: 10vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: $navbarColor;
    
    &__button{
        font-family: $fontParagraph;
        color: $colorTextPrincipal;
        font-weight: 700;
        font-size: 1.1em;
        border: 2px solid $colorTextPrincipalOpacityReduc;
        padding: 0.5em 2em;
        margin-right: 20px;
        border-radius: 50px;

        &:hover{
            background-color: $colorTextPrincipalOpacityReduc;
            transition: 0.3s;
        }
    }

}
#overlay {
  position: fixed; /* Sit on top of the page content */
  display: none; /* Hidden by default */
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.8); /* Black background with opacity */
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer; /* Add a pointer on hover */

  & .active {
    position: absolute;
    width: 400px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: #4070B3;
    border-radius: 10px;
    overflow: hidden;
    cursor: default;


    & figure {
        & img {
            width: 100%;
            height: 280px;
            display: block;
        }
    }

    .container_card_text_overlay {
        padding: 1.5rem 1rem;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: auto auto 190px auto;
        height: auto;
        background: $colorBgPrincipal;
    }

    & h3 {
        text-align: center;
        text-transform: uppercase;
    }

    & .short-desc {
        padding: 3rem 0 0 0;
    }

    & .description {
        padding: 2rem 0 2rem 0;
    }

    .card_btn_overlay {
        display: flex;
        justify-content: center;

        .btn-edit {
            border: none;
            background: #4070B3;
            color: white;
            padding: 0.5rem 1rem;
            border-radius: 5px;
            margin-right: 1rem;
            font-weight: 600;
            font-family: $fontTitle;
            cursor: pointer;
            transition: all 0.2s ease-in-out;

            &::after {
                content: 'EDIT';
            }

            &:hover {
                background: white;
                color: #4070B3;
            }
        }

        .btn-del {
            border: none;
            background: #b00101;
            color: white;
            padding: 0.5rem 1rem;
            border-radius: 5px;
            font-weight: 600;
            font-family: $fontTitle;
            cursor: pointer;
            transition: all 0.2s ease-in-out;

             &::after {
                content: 'DELETE';
            }

            &:hover {
                background: white;
                color: #b00101;
            }
        }
    }
  }
}

main {
    width: 100%;

    .section_character_list {
        padding: 5rem 3rem 3rem 3rem;

        h2 {
            font-size: 2rem;
            margin-bottom: 3rem;
        }

        .container_character_list {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 2rem;
            padding: 8rem 5rem 2rem 5rem;
            width: 1700px;
            border-radius: 1rem;
            margin: 0 auto;
            box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2) ;

            .character_list_card {
                width: 300px;
                overflow: hidden;
                box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5) ;
                margin: 0 auto;
                border-radius: 10px;
                transition: all 0.2s ease-in-out;
                margin-bottom: 3rem;

                &:hover {
                    transform: scale(1.02);
                    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.8) ;
                }

                figure {
                    img {
                        width: 100%;
                        height: 230px;
                        display: block;
                    }
                }

                .card_text_content {
                    padding: 1.5rem 1rem;
                    display: grid;
                    grid-template-columns: 1fr;
                    grid-template-rows: auto 1fr auto;
                    height: 250px;

                    h3 {
                        text-align: center;
                    }

                    p {
                        padding: 2rem 0;
                        align-self: center;
                    }

                    a {
                        text-align: center;
                        padding: 0.8rem 1.5rem;
                        border-radius: 5px;
                        background: #4070B3;
                        max-width: 200px;
                        margin: 0 auto;
                        color: $colorTextPrincipal;
                        transition: all 0.2s ease-in-out;
                        font-weight: 600;
                        margin-top: 1rem;
                        
                        &::after {
                            content: 'VIEW';
                        }

                        &:hover {
                            color: #4070B3;
                            background: $colorTextPrincipal;
                        }
                    }
                }
            }
        }
    }
}
