main {
    width: 100%;

    .section_create_character {
        padding: 5rem 3rem 3rem 3rem;

        h2 {
            font-size: 2rem;
            margin-bottom: 3rem;
        }

        .container_create_character {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 4rem;
            width: 1700px;
            border-radius: 1rem;
            margin: 0 auto;

            form {
                box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.5) ;
                display: flex;
                flex-direction: column;
                padding: 4rem 3rem 3rem 3rem;
                border-radius: 5px;
                width: 600px;
                background: #2b466f;

                .container_upload_file {
                    margin-bottom: 2rem;

                    #file-input {
                        display: none;

                        & + label {
                            padding: 1rem;
                            background: blue;
                            border: 1px solid white;
                            border-radius: 5px;
                            font-family: $fontTitle;
                            color: white;
                            font-weight: 600;
                            transition: all 0.2s ease-in-out;
                            cursor: pointer;

                            &:hover {
                                background: white;
                                color: blue;
                                border: 1px solid blue;
                            }
                        }

                    }

                    .container_btn_upload_img {
                        color: white;
                        font-family: $fontParagraph;

                        .btn_upload_text {
                            font-weight: 600;
                        }
                    }
                }

                & .input-create-text {
                    height: 50px;
                    background: none;
                    border: none;
                    border-bottom: 1px solid grey;
                    margin-bottom: 1rem;
                    padding: 1rem 1.5rem;
                    font-size: 1.2rem;
                    outline: none;
                    color: white;
                    letter-spacing: 0.1rem;
                }

                & textarea {
                    background: none;
                    border: 1px solid grey;
                    outline: none;
                    margin-top: 2rem;
                    font-size: 1rem;
                    color: white;
                    padding: 1rem 1.5rem;
                    margin-bottom: 3rem;
                }

                & .btn-create-send {
                    padding: 0.8rem 1.5rem;
                    border: 2px solid white;
                    background: green;
                    margin: 0 auto;
                    border-radius: 5px;
                    color: white;
                    font-weight: 600;
                    letter-spacing: 0.1rem;
                    font-size: 1.1rem;
                    cursor: pointer;
                    transition: all 0.2s ease-in-out;

                    &:hover {
                        background: rgb(232, 232, 232);
                        color: green;
                        border: 2px solid green;

                    }
                }

                & label {
                    color: white;
                }
            }
        }
    }
}
footer{
    height: 15vh;
    @include display_flex_row;
    background-color: $navbarColor;
}

.container_edit_character {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;


    .card_edit_character {
        box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.5) ;
        display: flex;
        flex-direction: column;
        padding: 4rem 3rem 3rem 3rem;
        border-radius: 5px;
        width: 600px;
        background: #2b466f;

        & figure {
            display: flex;
            flex-direction: column;
            margin: 0 auto;
            height: 150px;
            width: 150px;

            .img_edit_character {
                width: 100%;
                display: block;
            }
        }

        #file-input-edit {
            display: none;
            & + label {
                padding: 1rem;
                background: blue;
                border: 1px solid white;
                border-radius: 5px;
                font-family: $fontTitle;
                color: white;
                font-weight: 600;
                transition: all 0.2s ease-in-out;
                cursor: pointer;
                max-width: 180px;

                    &:hover {
                        background: white;
                        color: blue;
                        border: 1px solid blue;
                    }
            }
        }

        .container_chosen_file_edit {
            margin-top: 3rem;
            .container_btn_upload_img_edit {
                color: white;
                font-weight: 600;
                font-family: $fontTitle;
                font-size: 0.8rem;
            }
        }

        .card_text_content_edit {
            display: flex;
            flex-direction: column;
            margin-top: 3rem;

            .input_create_text_edit {
                height: 50px;
                background: none;
                border: none;
                border-bottom: 1px solid grey;
                margin-bottom: 1rem;
                padding: 1rem 1.5rem;
                font-size: 1.2rem;
                outline: none;
                color: white;
                letter-spacing: 0.1rem;
            }

            #description_edit {
                background: none;
                    border: 1px solid grey;
                    outline: none;
                    margin-top: 2rem;
                    font-size: 1rem;
                    color: white;
                    padding: 1rem 1.5rem;
                    margin-bottom: 3rem;
            }
        }

        .btn-save-edit {
            padding: 0.8rem 1.5rem;
            border: 2px solid white;
            background: green;
            margin: 0 auto;
            border-radius: 5px;
            color: white;
            font-weight: 600;
            letter-spacing: 0.1rem;
            font-size: 1.1rem;
            cursor: pointer;
            transition: all 0.2s ease-in-out;

            &:hover {
                background: rgb(232, 232, 232);
                color: green;
                border: 2px solid green;
            }
        }
    }
}

.container_redirect {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;

    i {
        font-size: 7rem;
        color: greenyellow;
        margin-bottom: 2rem;
    }

    p {
        font-family: $fontTitle;
        font-size: 1.5rem;
    }
}
